import React, { useEffect, useState } from "react";
import axios from "axios";

import NavBar from "../components/NavBar";

const ServerPage = () => {
  const [serverData, setServerData] = useState([]);
  const [clusterData, setClusterData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      let { data } = await axios.get("/api/vpn-server/server");
      let { servers, clusters } = data;
      servers = servers.map(server => ({
        ...server,
        cluster: clusters.find(cluster => cluster.clusterId === server.clusterId)
      }));
      setServerData(servers);
      setClusterData(clusters);
      setIsLoading(false);
    };

    fetchData();
  }, []);

  const rotationChange = async (action, ipAddress) => {
    const body = {
      server: {
        ipAddress: ipAddress
      }
    };
    if (action === "add") {
      await axios.request({
        method: "PUT",
        url: "/api/vpn-server/server/Z088920126Z9T7MQECE3M",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          ipAddress
        }
      });
    }
    if (action === "remove") {
      await axios.request({
        method: "DELETE",
        url: "/api/vpn-server/server/Z088920126Z9T7MQECE3M",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          ipAddress
        }
      });
    }
    return window.location.reload();
  };

  return (
    <React.Fragment>
      <NavBar />
      {isLoading ? (
        <h4>Fetching data..</h4>
      ) : (
        <table className="table">
          <thead>
            <tr>
              <th scope="col">Friendly Name</th>
              <th scope="col">Cluster</th>
              <th scope="col">DNS Status</th>
              <th scope="col">Actions</th>
            </tr>
          </thead>
          <tbody>
            {serverData.map(server => (
              <tr>
                <th scope="row">{server.friendlyName}</th>
                <td>{server.cluster?.name}</td>
                <td>{server.active ? server.cluster.subdomain : "Inactive"}</td>
                <td>
                  {!server.active ? (
                    <button onClick={() => rotationChange("add", server.ipAddress)}>Add to rotation</button>
                  ) : (
                    <button onClick={() => rotationChange("remove", server.ipAddress)}>Remove from rotation</button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </React.Fragment>
  );
};

export default ServerPage;
