import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";

// pages
import DNSEntriesPage from "./containers/DNSEntriesPage";
import ServerPage from "./containers/ServerPage";
import Error404Page from "./containers/error/Error404Page";
class Router extends Component {
  render() {
    return (
      <Switch>
        <Route exact path="/" component={ServerPage} />
        <Route exact path="/servers" component={ServerPage} />
        <Route exact path="/dns-entries" component={DNSEntriesPage} />
        <Route path="*" component={Error404Page}></Route>
      </Switch>
    );
  }
}

export default Router;
