import React, { useEffect, useState } from "react";
import axios from "axios";

import NavBar from "../components/NavBar";

const DNSEntriesPage = () => {
  const [recordData, setRecordData] = useState([]);
  const [clusterData, setClusterData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      let { data } = await axios.get("/api/vpn-server/a-records/Z088920126Z9T7MQECE3M");
      let { records, clusters } = data;

      console.log(records);
      setRecordData(records);
      setClusterData(clusters);
      setIsLoading(false);
    };

    fetchData();
  }, []);

  return (
    <React.Fragment>
      <NavBar />
      {isLoading ? (
        <h4>Fetching data..</h4>
      ) : (
        <table className="table">
          <thead>
            <tr>
              <th scope="col">Domain String</th>
              <th scope="col">IP</th>
              <th scope="col">Server Friendly Name</th>
              <th scope="col">Cluster Name</th>
            </tr>
          </thead>
          <tbody>
            {recordData.map(cluster => {
              return cluster.servers.map(server => (
                <tr>
                  <th scope="row">{cluster.name}</th>
                  <td>{server.ipAddress}</td>
                  <td>{server.friendlyName}</td>
                  <td>{clusterData.find(c => c.clusterId === server.clusterId)?.name}</td>
                </tr>
              ));
            })}
          </tbody>
        </table>
      )}
    </React.Fragment>
  );
};

export default DNSEntriesPage;
